import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import swal from "sweetalert";
import Footer from "../../Components/Footer";
import LoggedHeader from "../../Components/Header";
import Loading from "../../Components/Loading";
import { axiosGet } from "../../utils/axiosRequest";

function Invite() {
   
    const [phone , setPhone] = useState('');
    const [code , setCode] = useState(+1);
    const[isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();

    useEffect(() => { 
        const Id = localStorage.getItem('id');
        if(Id === null){
            navigate('/login-signup')
        }
    })
    
    function invite(e){
        e.preventDefault();
        setIsLoading(true)
        const url = `/subscription/send?countryCode=${code}&phone=${phone}`;

        axiosGet( url, (callBack) => {
            if (callBack?.status === 200) {
                swal("Invite Successfully send");
                setIsLoading(false)
                navigate('/dashboard');
                
            } else {
                setIsLoading(false)
                alert(callBack.message)
            }
        })
    }

    return(
        <div>
            
            {isLoading ? <Loading/> : false}
            <LoggedHeader/>

            <main className="login-wrapper">
                <div className="container">
                        <div className="login-left moible-wrap">
                            <h2>Enter mobile number</h2>
                            <p>Invite you Friend and get Free Credit</p>
                            <form onSubmit={invite}>
                            <div className="login-content mobile-cntnt">

                                <div className="login-item selectCode">
                                    <div className="phoneCode">
                                      {/* <label htmlFor="countryCode">Code</label> */}
                                        <select name="countryCode" id="countryCode" onChange={(e)=>setCode(e.target.value)}>
                                            <option value={+1}>USA (+1)</option>
                                            <option value="+44" >UK (+44)</option>
                                            <option value="+91" >IN (+91)</option>
                                        </select>  
                                    </div>
                                    <div className="phoneVerification">
                                        {/* <label htmlFor="lg1">Enter Mobile Number</label> */}
                                        <input type='tel' id="lg1" placeholder="Mobile Number" name="phone" onChange={(e) => setPhone(e.target.value)} required />  
                                    </div>

                                    
                                </div>

                                <div className="login-item">
                                    <button type="submit" >Send Invite</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </main>

        <Footer/>
        </div>
    )
    
}

export default Invite;