import menu from "../images/menu.png";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { SaveStorageItems } from "../utils/AsyncStorage";
import { axiosGet } from "../utils/axiosRequest";
import { loginDetails } from "../redux/action";
import { useEffect } from "react";

function DashboardWrapper() {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const Id = localStorage.getItem('id');
    

    useEffect(() => { 

        if(Id === null){
            navigate('/login-signup')
        }

        const url = `/user/${Id}`;
        axiosGet(url, (callBack) => {
            if (callBack?.status === 200) {
                SaveStorageItems(
                    "loginDetails",
                    JSON.stringify(callBack.data),
                    0,
                    () => {
                        dispatch(loginDetails({ payload: callBack?.data }));
                    }
                );
                
            }else{
                alert(callBack.message)
            }
        })

    })

    function change(value){
        navigate(`/update${value}`)
    }

    function invite(){
        navigate('/invite')
    }

    return(
        <div className="row">
            <div className="col-md-6 col-lg-4">
                <div className="dashboard-card">
                    <h2>Pay As You Go <img src={menu} alt="" /></h2>
                    <p>$6.99 per background check</p>
                    <Link className="subscrip-btn Link" to="/subscriptionPage">Subscriptions</Link>
                    <p style={{fontSize:'small'}}>Save money with a subscription</p>
                </div>
            </div>
            <div className="col-md-6 col-lg-4">
                <div className="dashboard-card">
                    <h2>0 Free Credits <img src={menu} alt="" /></h2>
                    <h6>Earn Free Credits</h6>
                    <ul>
                        <li><a>Watch Ads</a></li>
                        <li><a onClick={invite}>Invite Friends</a></li>
                    </ul>
                </div>
            </div>
            <div className="col-md-6 col-lg-4">
                <div className="dashboard-card">
                    <h2>My Account <img src={menu} alt="" /></h2>
                    <ul>
                        <li><a onClick={() => change("Email",)}>Change Email Address</a></li>
                        <li><a onClick={() => change("Password")}>Change Password</a></li>
                        <li><a onClick={() => change("Phone")}>Change Mobile Number</a></li>
                    </ul>
                </div>
            </div>
        </div>
    )
    
}

export default DashboardWrapper;