import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import swal from "sweetalert";
import Footer from "../../../Components/Footer";
import {Header} from "../../../Components/Header";
import Loading from "../../../Components/Loading";
import { axiosPost } from "../../../utils/axiosRequest";
function PhoneVerification() {
   
    const [phone , setPhone] = useState('');
    const [code , setCode] = useState(+1);
    const[isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();

    useEffect(() => { 
        window.scrollTo(0, 0);
        const Id = localStorage.getItem('id');
        if(Id === null){
            navigate('/login-signup')
        }
    })

    function mobVerify(e){
        e.preventDefault();
        setIsLoading(true)
        const url = "/user/register";
        const obj = {
            phone: phone,
            countryCode: code,
            mode:'phone'
        };
        
        localStorage.setItem('phone', phone)
        axiosPost(url, obj, (callBack) => {
            
            if (callBack?.status === 200) {
                swal("An OTP Successfully send");
                setIsLoading(false)
                navigate('/phoneVerificationOtp');
                
            } else {
                setIsLoading(false)
                alert(callBack.message)
            }
        })
    }

    return(
        <div>
            
            {isLoading ? <Loading/> : false}
            <Header/>

            <main className="login-wrapper">
                <div className="container">
                        <div className="login-left moible-wrap">
                            <h2>Confirm mobile number</h2>
                            <p>We do this for security and preventing bots</p>
                            <form onSubmit={mobVerify}>
                            <div className="login-content mobile-cntnt">

                                <div className="login-item selectCode">
                                    <div className="phoneCode">
                                        <select name="countryCode" id="countryCode" onChange={(e)=>setCode(e.target.value)}>
                                            <option value="+1">USA (+1)</option>
                                            <option value="+44" >UK (+44)</option>
                                            <option value="+91" >IN (+91)</option>
                                        </select>  
                                    </div>
                                    <div className="phoneVerification">
                                        <input type='number' id="lg1" placeholder="Mobile Number" name="phone" onChange={(e) => setPhone(e.target.value)} required/>  
                                    </div>

                                    
                                </div>

                                <div className="login-item">
                                    <button type="submit" >Send Code</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </main>

        <Footer/>
        </div>
    )
    
}

export default PhoneVerification;