import logo from "../images/logo.png"
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {  logOut } from "../redux/action";
import { useEffect } from "react";
  

export function Header(){
    useEffect(() => {
        window.scrollTo(0, 0)
        }, [])
        function toggle(name){
        const element = document.getElementById(name);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }};
     
    function menu() {
        const element = document.getElementById('none-menu');
        if (element.style.display === "block") {
            element.style.display = "none"
        }else{
            element.style.display = "block"
        }};

    return (
    <div>
            
        <header className="header-area">
            <div className="container">
                <nav className="navbar navbar-expand-lg">
                    <a className="navbar-brand" href="#">
                        <img src={logo} alt=""/>
                    </a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasExample" aria-controls="offcanvasExample">
                        <span onClick={menu} className="hamburger-box">
                            <span  className="hamburger-inner"></span>
                        </span> 
                    </button>
                    <div className="collapse navbar-collapse">
                        <ul className="navbar-nav ms-auto align-items-center">
                            <li><a href="/" id="active">Home</a></li>
                            <li><a onClick={() => toggle("about")} >About</a></li>
                            <li><a onClick={() => toggle("freeSearch")} >Free Search</a></li>
                            <li><a onClick={() => toggle("pricing")} >Pricing</a></li>             
                            <li><a onClick={() => toggle("contact")}>Contact</a></li>   
                            <li><Link to="/login-signup" className="login-btn loginactive Link">Login / Sign Up</Link></li>           
                        </ul>
                    </div>
                </nav>
            </div>

            <div className="none-menu" id="none-menu">
                <ul className="navbar-nav">
                    <li><a href="/" id="active">Home</a></li>   
                    <li><a onClick={() => toggle("about")} >About</a></li>
                    <li><a onClick={() => toggle("freeSearch")}  >Free Search</a></li>
                    <li><a onClick={() => toggle("pricing")}  >Pricing</a></li>             
                    <li><a onClick={() => toggle("contact")} >Contact</a></li>   
                    <li> <Link className="login-btn loginactive Link" to="/login-signup"> Login / Sign Up </Link></li>
                </ul>
            </div>
        </header>

    </div>
    )

}

 function LoggedHeader(){
    useEffect(() => {
        window.scrollTo(0, 0)
      }, [])
    const userData = useSelector((state) => state);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    function logingout(){
        dispatch(logOut());
        console.log(userData);
        navigate('/')
    }

    return(

        <header className="header-area">
            <div className="container">
                <div className="header-main">
                    <div className="header-logo">
                        <p className="navbar-brand" >
                            <img src={logo} alt=""/>
                        </p>
                    </div>
                    <ul className="navbar-nav">
                    <li><button className="login-btn loginactive Link" onClick={logingout}> Log Out </button></li>
                    </ul>
                </div>
            </div>
        </header>
    )
};

export default LoggedHeader;
