import Footer from "../../../Components/Footer";
import LoggedHeader from "../../../Components/Header";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { axiosPut } from "../../../utils/axiosRequest";
import swal from "sweetalert";
import Loading from "../../../Components/Loading";

function UpdatePhone() {

    const userData = JSON.parse(localStorage.getItem('loginDetails'));
    const[phone, setPhone] = useState('');
    const[oldPhone, setOldPhone] = useState('');
    const[isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();

    useEffect(() => { 
        window.scrollTo(0, 0);
        const Id = localStorage.getItem('id');
        if(Id === null){
            navigate('/login-signup')
        }
    })

    function PhoneChanger(e){

        e.preventDefault();
        setIsLoading(true);
        const id = userData._id
        const url = `/user/${id}`;
        const obj = {
            phone : phone
        }
        const oldtel = (userData.phone)

        if ( oldtel === oldPhone) {
            axiosPut(url, obj, (callBack) => {

            if (callBack?.status === 200) {
                setIsLoading(false)
                swal('Phone Sucessfully Upadated')
                navigate('/dashboard')

            }else{
                setIsLoading(false)
                alert(callBack.message)
            }})  

        } else {
            setIsLoading(false)
            swal('Invalid Old Phone Number !!')
        }
    }


    return(
        <div>
            
            {isLoading ? <Loading/> : false}
            <LoggedHeader/>

                <main className="login-wrapper">
                    <div className="container">
                        <div className="login-left moible-wrap">
                            <h2>Update Phone No.</h2>
                            <form onSubmit={PhoneChanger}>
                                <div className="login-content mobile-cntnt">
                                    <div className="login-item">
                                        <label htmlFor="lg1">Old Phone</label>
                                        <input className="Phone" type="number" minLength="10" maxLength='10' placeholder="Old Phone" onChange={(e)=> setOldPhone(e.target.value)} required />
                                    </div>

                                    <div className="login-item">
                                        <label htmlFor="lg1">New Phone</label>
                                        <input className="Phone" type="number" minLength="10" maxLength='10' id="newtel" name="newtel" placeholder="New Phone" onChange={(e)=> setPhone(e.target.value)} required />
                                    </div>

                                    <div className="login-item mt-5">
                                        <button type="submit"> UPDATE </button>
                                    </div>

                                </div>
                            </form>
                        </div>
                    </div>
                </main>

            <Footer/>
        </div>
    )
    
}

export default UpdatePhone;