import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import swal from "sweetalert";
import Footer from "../../../Components/Footer";
import {Header} from "../../../Components/Header";
import Loading from "../../../Components/Loading";
import { axiosPost } from "../../../utils/axiosRequest";

function PhoneVerificationOtp() {

    const[otp, setOtp] = useState('');
    const[isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();
    const userData = useSelector((state) => state.loginDetails);

    useEffect(() => { 
        window.scrollTo(0, 0);
        const Id = localStorage.getItem('id');
        if(Id === null){
            navigate('/login-signup')
        }
    })

    function verify(e){    
        e.preventDefault();
        setIsLoading(true)
        const url = '/user/verifyUser';
        const obj = {
            phone: localStorage.getItem('phone'),
            code: otp,
            email: userData.email,
            fullName: userData.fullName

        }
        axiosPost(url, obj, (callBack) => {
            if (callBack?.status === 200) {
                const url = `/user/verifyForgetPassword/${localStorage.getItem('id')}`
                localStorage.setItem('url', url)
                swal("You are Succesfully Registered");
                setIsLoading(false)
                navigate('/dashboard')
            }else{
                setIsLoading(false)
                swal(callBack.message)
            }
        })
    }


    return(
        <div>
            
            {isLoading ? <Loading/> : false}
            <Header/>

            <main className="login-wrapper">
                <div className="container">
                    <div className="login-left moible-wrap">
                        <h2>Enter OTP code</h2>
                        <p>We do this for security and preventing bots</p>
                        <form onSubmit={verify}>
                            <div className="login-content mobile-cntnt">
                                <div className="login-item">
                                    <label htmlFor="lg1">Enter OTP code below</label>
                                    <input type="text" id="lg1" placeholder="OTP" name="otp" onChange={(e)=> setOtp(e.target.value)} required />
                                </div>

                                <div className="login-item">
                                    <button type="submit">Verify</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </main>

            <Footer/>
        </div>
    )
    
}

export default PhoneVerificationOtp;