import Footer from "../../../Components/Footer";
import LoggedHeader from "../../../Components/Header";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { axiosPut } from "../../../utils/axiosRequest";
import swal from "sweetalert";
import Loading from "../../../Components/Loading";


function UpdatePassword() {

    const[password, setPassword] = useState('');
    const[confirmPassword, setConfirmPassword] = useState('');
    const[isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();

    useEffect(() => { 
        window.scrollTo(0, 0);
        const Id = localStorage.getItem('id');
        if(Id === null){
            navigate('/login-signup')
        }
    })

//Password Update Function

    function passwordChanger(e){
        e.preventDefault();
        setIsLoading(true);
        const url = `/user/${localStorage.getItem('id')}`;
        const obj = {
            password : password
        }
        if (confirmPassword === password) {
            axiosPut(url, obj, (callBack) => {
            if (callBack?.status === 200) {
                setIsLoading(false)
                swal('Password Sucessfully Upadated')
                navigate('/dashboard')

            }else{
                setIsLoading(false)
                alert(callBack.message)
            }})  

        } else {
            setIsLoading(false)
            swal('Password Must Be Same !!')
        }
    }

//Random Function

function myFunction( name, eyes ) {    
    var x = document.querySelector(`input[name=${name}]`);
    var y = document.querySelector(`#${eyes}`)
    if (x.type === "password" || y.src === 'https://w7.pngwing.com/pngs/221/536/png-transparent-eye-password-see-view-minimalisticons-icon-thumbnail.png') {
      x.type = "text";
      y.src = 'https://w7.pngwing.com/pngs/910/805/png-transparent-eye-key-look-password-security-see-view-minimalisticons-icon-thumbnail.png'
    } else {
      x.type = "password";
      y.src = 'https://w7.pngwing.com/pngs/221/536/png-transparent-eye-password-see-view-minimalisticons-icon-thumbnail.png'
    }
  }

    return(
        <div>
            
            {isLoading ? <Loading/> : false}
            <LoggedHeader/>

                <main className="login-wrapper">
                    <div className="container">
                        <div className="login-left moible-wrap">
                            <h2>Create New Password</h2>
                            <form onSubmit={passwordChanger}>
                                <div className="login-content mobile-cntnt">    
                                    <div className="login-item">
                                        <label htmlFor="newPass">New Password</label>
                                        <div className="password">
                                            <input minLength="8" type="password" id="newPass" name="newPass" placeholder="New Password" onChange={(e)=> setPassword(e.target.value)} required />
                                            <img src='https://w7.pngwing.com/pngs/221/536/png-transparent-eye-password-see-view-minimalisticons-icon-thumbnail.png' 
                                            alt="" name='eye4' id='eye4' width='30px' onClick={()=>myFunction('newPass','eye4')} />
                                        </div>
                                    </div>

                                    <div className="login-item">
                                        <label htmlFor="lg1">Repeat New Password</label>
                                        <div className="password">
                                            <input minLength="8" type="password" id="repeatNewPass" name="repeatNewPass" placeholder="Re-Enter Password" onChange={(e)=> setConfirmPassword(e.target.value)} required />
                                            <img src='https://w7.pngwing.com/pngs/221/536/png-transparent-eye-password-see-view-minimalisticons-icon-thumbnail.png' 
                                            alt="" name='eye5' id='eye5' width='30px' onClick={()=>myFunction('repeatNewPass','eye5')} />
                                        </div>
                                    </div>

                                    <div className="login-item mt-5">
                                        <button type="submit"> UPDATE </button>
                                    </div>

                                </div>
                            </form>
                        </div>
                    </div>
                </main>

            <Footer/>
        </div>
    )
    
}

export default UpdatePassword;