import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import swal from "sweetalert";
import Footer from "../../Components/Footer";
import LoggedHeader from "../../Components/Header";
import Loading from "../../Components/Loading";
import { axiosGet } from "../../utils/axiosRequest";

function Success() {

    const[isLoading, setIsLoading] = useState(false);

    const navigate = useNavigate();

    const params = new URLSearchParams(window.location.search);
    const orderId = params.get("orderId");
    const paymentId = params.get("paymentId");
    const token = params.get("token");
    const PayerID = params.get("PayerID");

    useEffect(() => {
    const url = `/orders/successPayment/${orderId}?paymentId=${paymentId}&token=${token}&PayerID=${PayerID}`
    console.log(url);

        axiosGet(url, (callBack) => {
            if(callBack?.status === 200){
                setIsLoading(false)
                swal("Transaction Successful")
                const url = '/dashboard'
                setTimeout(navigate(url), 10000);

            }else{
                swal("Something Went Wrong")
                console.log(callBack.message);
            }
        })
    });


    return(

        <div>

            { isLoading ? <Loading/> : false }
            <LoggedHeader/>

            <main className="login-wrapper">
                <div className="container">
                    <div className="login-left moible-wrap">
                        <br />
                        <br />
                        <br />
                        <p>Congrates!!🎉🎉</p>
                        <br />
                        <h2>Payment Successful 😉</h2>
                        <br />
                        <br />
                        <b><Link to='/dashboard'>Dashboard</Link></b>
                    </div>
                </div>
            </main>

            <Footer/>
        </div>
    )
    
}

export default Success;