import Footer from "../../../Components/Footer";
import LoggedHeader from "../../../Components/Header";
import { Link, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { axiosPost } from "../../../utils/axiosRequest";
import swal from "sweetalert";
import Loading from "../../../Components/Loading";

function CreateNewPass() {

    const[password, setPassword] = useState('');
    const[confirmPassword, setConfirmPassword] = useState('');
    const[isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();

    useEffect(() => { 
        window.scrollTo(0, 0);
        const Id = localStorage.getItem('id');
        if(Id === null){
            navigate('/login-signup')
        }
    })

    function passwordChanger(e){

        e.preventDefault();
        const url = `/user/verifyForgetPassword/${localStorage.getItem('id')}`;
        const obj = {
            password : password
        }
        if (confirmPassword === password) {
            setIsLoading(true)
            axiosPost(url, obj, (callBack) => {
            if (callBack?.status === 200) {
                swal('Password Sucessfully Upadated', 'Please Login')
                setIsLoading(false)
                navigate('/login-signup')
            }else{
                setIsLoading(false)
                alert(callBack.message)
            }})  
        } else {
            setIsLoading(false)
            swal('Password Must Be Same !!')
        }
    }


    return(
        <div>
            
        {isLoading ? <Loading/> : false}
            <LoggedHeader/>

                <main className="login-wrapper">
                    <div className="container">
                        <div className="login-left moible-wrap">
                            <h2>Create new password</h2>
                            <form onSubmit={passwordChanger}>
                                <div className="login-content mobile-cntnt">    
                                    <div className="login-item">
                                        <label htmlFor="conNewPass">New Password</label>
                                        <input className="password" minLength="8" type="password" id="conNewPass" name="newPass" placeholder="New Password" onChange={(e)=> setPassword(e.target.value)} required />
                                    </div>

                                    <div className="login-item">
                                        <label htmlFor="lg1">Repeat New Password</label>
                                        <input className="password" minLength="8" type="password" id="repeatNewPass" name="repeatPass" placeholder="Re-Enter Password" onChange={(e)=> setConfirmPassword(e.target.value)} required />
                                    </div>

                                    <div className="login-item mt-5">
                                        <button type="submit"> UPDATE </button>
                                    </div>
                                    
                                    <div className="login-links">
                                        <p>Already have an account? <Link className="Link"  to="/login-signup">Login here</Link></p>
                                        <p>Don’t have an account? <Link className="Link"  to="/login-signup">Signup here</Link></p>
                                    </div>

                                </div>
                            </form>
                        </div>
                    </div>
                </main>

            <Footer/>
        </div>
    )
    
}

export default CreateNewPass;