import swal from "sweetalert";
import Footer from "../../Components/Footer";
import { useDispatch} from "react-redux";
import { useNavigate } from "react-router-dom";
import { Header }from "../../Components/Header";
import { loginDetails } from "../../redux/action";
import { SaveStorageItems } from "../../utils/AsyncStorage";
import { axiosGet } from "../../utils/axiosRequest";
import { useEffect, useState } from "react";
import Loading from "../../Components/Loading";

function Redirect() {

    const userData = JSON.parse(localStorage.getItem('loginDetails'));
    const[isLoading, setIsLoading] = useState(false);
    const navigate = useNavigate();
    const dispatch = useDispatch();

    useEffect(() => { 
        const Id = localStorage.getItem('id');
        if(Id === null){
            navigate('/login-signup')
        }
    })

    function check(){ 

        setIsLoading(true);
        const  id = userData._id
        const url = `/user/${id}`;
        axiosGet(url, (callBack) => {
            
            if (callBack?.status === 200) {
                SaveStorageItems(
                    "loginDetails",
                    JSON.stringify(callBack.data),
                    0,
                    () => {
                        dispatch(loginDetails({ payload: callBack?.data }));
                    }
                );
                localStorage.setItem('id', callBack.data._id)
                const verified = localStorage.getItem('verify');
                const userData = JSON.parse(localStorage.getItem('loginDetails'))
                if (verified === "verifyForgot") {
                    if (userData.verifyForgot === true){
                        setIsLoading(false)
                        swal("Forgot Email Verified")
                        const url = localStorage.getItem('url');
                        navigate(url);
                    } else {
                        setIsLoading(false)
                        swal(" Forgot Email Not Verified Yet ")
                    } 
                } else {

                    if (userData.emailVerified === true){
                        setIsLoading(false)
                        swal("Email Verified")
                        const url = localStorage.getItem('url');
                        navigate(url);
                    } else {
                        setIsLoading(false)
                        swal("Email Not Verified Yet ")
                    } 
                }
                
            }else{
                setIsLoading(false);
                alert(callBack.message);
            }
        })

    
    }
    return(
        <div>
            
            {isLoading ? <Loading/> : false}
            <Header/>
            
            <main className="login-wrapper">
                <div className="container">
                    <div className="login-left moible-wrap" style={{textAlign: 'center'}}>
                        <br />
                        <br />
                        <br />
                        <h2>Verification Email Send to Your Email Address</h2>
                        <br />
                        <br />
                        <p>You can countinue the <b>Verification</b></p>
                        <br />
                        <br />
                        <b><a onClick={check}>Check Verification</a></b>
                                
                    </div>
                </div>
            </main>

            <Footer/>
        </div>
    )   
}

export default Redirect;