import axios from "axios";
import { MainUrl } from "./mainUrl";

const axiosAPI = axios.create({
  baseURL: MainUrl,
  headers: {
    "Content-Type": "application/json",
    'Access-Control-Allow-Origin':'http://localhost:3000',
    "Authorization": `Bearer ${localStorage.getItem('bearer')}`
}});

export async function axiosPost(url, obj, callBack){

  try {
    const response = await axiosAPI.post(url, obj);
    const data= (response.data);
    callBack(data);

  } catch (error) {
    if (error.response) {
      console.log(
        "🚀 ~ file: axiosRequest.js ~ line 22 ~ axiosPOSTCall ~ err.response",
        error.response.data
      );
      callBack(error?.response?.data);

    } else {
      callBack(error);

  }}
}

export async function axiosGet(url, callBack) {

  try {
    const response = await axiosAPI.get(url);
    const data= (response.data);
    callBack(data);

  } catch (error) {
    if (error.response) {
      console.log(
        "🚀 ~ file: axiosRequest.js ~ line 45 ~ axiosPOSTCall ~ err.response",
        error.response.data
      );
      callBack(error?.response?.data);
    } else {
      callBack(error);
  }}
};



