import React from "react";
import DashboardWrapper from "../../Components/DashboardWrapper";
import Footer from "../../Components/Footer";
import LoggedHeader from "../../Components/Header";
import { Link } from "react-router-dom";
import SearchArea from "../../Components/SearchArea";
import location from "../../images/location.png"

function ResultPage() {
    return(
        <div>
            <LoggedHeader/>
            <div className="dashboard-wrapper">
                <div className="container">
                    <DashboardWrapper/>
                    <SearchArea/>

                    <div className="result-part">
                        <div className="result-heading">
                            <p>100 Results found for <strong>John Smith</strong> in <strong>All States</strong> <br/> <a>Refine Search</a></p>
                        </div>
                    </div>

                    <div className="result-title">
                        <h2>100+ Results</h2>
                    </div>

                    <div className="result-box">
                        <div className="result-left">
                            <h4>John Smith</h4>
                            <p>47 Years Old</p>
                            <Link to="/record-page" className="d-none d-sm-inline-block Link">View Report</Link>
                        </div>
                        <div className="result-right">
                            <img src={location} alt="" />
                            <p>Glen Cove, NY <br/>Manhasset, NY <br/>Franklin Square, NY <br/>Farmingdale, NY</p>
                        </div>
                        <Link to="/record-page" className="d-sm-none Link">View Report</Link>
                    </div>
                    <div className="result-box">
                        <div className="result-left">
                            <h4>John Smith</h4>
                            <p>21 Years Old</p>
                            <Link to="/record-page" className="d-none d-sm-inline-block Link">View Report</Link>
                        </div>
                        <div className="result-right">
                            <img src={location} alt="" />
                            <p>Newark, NJ <br/>Brooklyn, NY</p>
                        </div>
                        <Link to="/record-page" className="d-sm-none">View Report</Link>
                    </div>
                    <div className="result-box">
                        <div className="result-left">
                            <h4>John Smith</h4>
                            <p>23 Years Old</p>
                            <Link to="/record-page" className="d-none d-sm-inline-block Link">View Report</Link>
                        </div>
                        <div className="result-right">
                            <img src={location} alt="" />
                            <p>Hollywood, FL <br/>Tampa, FL <br/>Miami, FL</p>
                        </div>
                        <Link to="/record-page" className="d-sm-none">View Report</Link>
                    </div>
                </div>
            </div>
            <Footer/>
        </div>
    )
    
}

export default ResultPage;